import { Component, OnInit } from '@angular/core';
import {RiskzoneService} from './riskzone.service';
import { RiskZone } from './riskzone';

@Component({
  selector: 'app-riskzone',
  templateUrl: './riskzone.component.html',
  styleUrls: ['./riskzone.component.css']
})
export class RiskzoneComponent implements OnInit {

  riskZoneList: RiskZone[]
  constructor(

    private riskZoneService:RiskzoneService

  ) { }

  ngOnInit(): void {
  
  this.getRiskZones();
  
  }
  getRiskZones(){
    this.riskZoneService.getRiskZoneList().subscribe((data:any):void=>{
      this.riskZoneList = data;
    })
  }


}
